* {
  box-sizing: border-box;
}

ul, li, h1, p, button, section, div {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

* {
  font-family: Roboto, sans-serif;
}

.hero__content {
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero__game {
  margin: 0 100px;
}

@media screen and (max-width: 1000px) {
  .hero__game {
    margin: 0;
  }
}

.hero__list {
  width: 450px;
  height: 450px;
  z-index: 1000;
  border-radius: 20px;
  flex-wrap: wrap;
  display: flex;
  position: relative;
}

@media screen and (max-width: 1000px) {
  .hero__list {
    width: 300px;
    height: 300px;
  }
}

.hero__item {
  width: 33.3333%;
  height: 33.3333%;
  cursor: pointer;
  background-color: #f9f9f9;
  border: 1px solid #e5852b;
  transition: all .3s;
  position: relative;
}

.hero__item:hover {
  background-color: #e4e4e4;
}

.hero__item:nth-child(1) {
  border-radius: 18px 0 0;
}

.hero__item:nth-child(3) {
  border-radius: 0 18px 0 0;
}

.hero__item:nth-child(7) {
  border-radius: 0 0 0 18px;
}

.hero__item:nth-child(9) {
  border-radius: 0 0 18px;
}

.hero__buttons {
  height: 80px;
  background-color: #ededed;
  border-radius: 0 0 20px 20px;
  display: flex;
  position: relative;
  top: -20px;
  box-shadow: 1px 28px 103px -27px rgba(0, 0, 0, .8);
}

.hero__button {
  color: #545454;
  width: 50%;
  border: none;
  border-radius: 0 0 0 20px;
  padding: 20px 0 0;
  font-size: 16px;
  transition: all .3s;
  display: block;
}

.hero__button:hover {
  background-color: #e4e4e4;
}

.hero__button:last-child {
  border-radius: 0 0 20px;
}

.hero__button--orange {
  animation: animationColor 1.5s infinite;
}

.hero__button--orange:hover {
  background-color: #e5852b;
}

@keyframes animationColor {
  0% {
    background-color: #e5852b;
  }

  50% {
    background-color: #ededed;
  }

  100% {
    background-color: #e5852b;
  }
}

.hero__svg {
  fill: #545454;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero__svg--hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.hero__svg-main {
  fill: #545454;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.hero__result {
  margin-top: 150px;
}

@media screen and (max-width: 1000px) {
  .hero__result {
    display: none;
  }
}

.hero__player {
  color: #545454;
  text-align: center;
  margin-bottom: 30px;
  font-size: 18px;
}

.hero__player--number {
  color: #e5852b;
  background-color: #f9f9f9;
  border-radius: 20px;
  padding: 30px 40px;
  box-shadow: 1px 28px 60px -27px rgba(0, 0, 0, .3);
}

.hero__number {
  margin-top: 20px;
  font-size: 24px;
  font-weight: 700;
  display: block;
}

.hero__queue {
  text-align: center;
  color: #545454;
  margin-bottom: 20px;
  font-size: 24px;
}

@media screen and (max-width: 1000px) {
  .hero__queue {
    display: none;
  }
}

